
import { defineComponent } from "vue";
import { useStore } from "vuex";
import CartIndicator from "@/components/CartIndicator.vue";
import { CartProductsData, FailureRequest } from "@/types";
import { z } from "zod";

export interface SuccessRequets {
  success: true;
  data: z.infer<typeof CartProductsData>;
}
export type ResponseData = SuccessRequets | FailureRequest;

export default defineComponent({
  components: { CartIndicator },
  setup() {
    const $store = useStore();

    const userAccessKey = localStorage.getItem("userAccessKey");
    $store.commit("setUserAccessKey", userAccessKey);

    $store.dispatch("cart/loadCart").then((response: ResponseData) => {
      if (response.success) {
        if (!userAccessKey) {
          $store.commit("setUserAccessKey", response.data.user.accessKey);
          localStorage.setItem("userAccessKey", response.data.user.accessKey);
        }
        $store.commit("cart/updateCartProductData", response.data.items);
      } else {
        // сообщение об ошибке
        console.log("Сообщение об ошибке");
      }
    });

    return {
      // amount,
    };
  },
});
