
import { defineComponent, ref, computed, watch } from "vue";
import numberFormat from "@/helpers/numberFormat";
import BaseButton from "@/components/BaseButton.vue";
import BaseLoaderCircle from "@/components/BaseLoaderCircle.vue";
import BaseCharacteristicProduct from "@/components/BaseCharacteristicProduct.vue";
import BaseNotification from "@/components/BaseNotification.vue";
import { useRoute } from "vue-router";
import useProductItem from "@/hooks/useProductItem";
import mutateProductData from "@/helpers/mutateProductData";
import declensionWord from "@/helpers/declensionWord";
import useOrder from "@/hooks/useOrder";
export default defineComponent({
  components: {
    BaseLoaderCircle,
    BaseCharacteristicProduct,
    BaseButton,
    BaseNotification,
  },
  setup() {
    const $route = useRoute();
    const {
      productFullData,
      productFullDataLoad,
      loadFullDataProduct,
      reassemblyColors,
      picIsLoad,
      img,
      checkLoad,
      addProductToCart,
      statusProduct,
      colorId,
      sizeId,
      // getCurrentProductFromCart,
      loaderCircle,
      chosenGalleryItem,
      chooseImage,

      currentAddedProduct,
    } = useProductItem();
    const { totalAmount } = useOrder();

    const currentTab = ref<number>(1);
    const quantity = ref<number>(1);

    loadFullDataProduct(+$route.params.id);

    const product = computed(() => {
      if (!productFullData.value) return null;
      else return mutateProductData(productFullData.value);
    });

    watch(img, () => {
      if (img.value) {
        picIsLoad.value = false;
        checkLoad();
      }
    });

    watch(colorId, () => {
      if (product.value) chooseImage(product.value.colors);
    });

    let timeout: number | undefined;
    watch(statusProduct, () => {
      if (statusProduct.productIsAdded) {
        clearTimeout(timeout);
        timeout = setTimeout(() => {
          statusProduct.productIsAdded = null;
        }, 3000);
      }
    });

    return {
      productFullData,
      picIsLoad,
      productFullDataLoad,
      reassemblyColors,
      product,
      numberFormat,
      colorId,
      sizeId,
      img,
      currentTab,
      quantity,
      addProductToCart,
      loaderCircle,
      statusProduct,
      chosenGalleryItem,
      totalAmount,
      declensionWord,

      currentAddedProduct,
    };
  },
});
